.App {
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  font-size: 20px;
  line-height: 1.5;
}

.sides {
  padding: 0px 30px;
}

.width-control {
  max-width: 1200px;
}

.project-info {
  text-align: left;
}


.row-flex {
  display: flex;
  flex-wrap: wrap;
}

.col-100 {
  flex: 0 0 100%;
    /* flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 100%; */
  max-width: 100%;
}

@media screen and (min-width: 1024px) {
  .col-50 {
    /* flex: 0 0 50%; */
    max-width: 40% !important;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 1024px) {
  .col-50 {
    /* flex: 0 0 50%; */
    max-width: 90%;
    /* max-width: 40% !important; */
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media screen and (min-width: 1024px) {
  .col-lg-33 {
    flex: 0 0 33.333%!important;
    width: 33.333%!important;
  }
}

.invisible {
  display: none;
  visibility: hidden;
}

.main {
  height: calc(100vh - 80px - 56px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.section {
  display: inline-block;
  width: 100%;
}

.rotate90 {
  transform:          rotate(90deg);
  -ms-transform:      rotate(90deg);
  -moz-transform:     rotate(90deg);
  -webkit-transform:  rotate(90deg);
  -o-transform:       rotate(90deg);
}

.text {
  text-align: left;
  line-height: 1.8
}

.font-manjari {
  font-family: 'Manjari', sans-serif
}

.center-text {
  text-align: center
}

.nav-home {
  list-style: none;
  text-decoration: none;
  display: inline-block;
  vertical-align: center;
}

a {
  text-decoration: none !important;
}

.navbar {
  display: flex;
  justify-content: center;
  height: 75px;
  background-color: transparent
}

.nav-links {
  display:flex;
  list-style: none;
  align-content: flex-end;
}

.nav-item a {
  display: inline-block;
  text-decoration: none;
  color: black;
  margin-left: 15px;
  margin-right: 15px;
}

.nav-item:hover a {
  border-bottom: 2px pink solid;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;
  border-top: 1px solid #e5d08f;
  height: 50px;
  color:  black;
}

.contact {
  display: flex;
  font-size: 1.3em;
  max-width: 1200px;
  width: 100%;
  justify-content: flex-end;
}

ul {
  list-style-type: none;
  padding-inline-start: 0px;
}

.info-title {
  margin: 10px 20px;
}

.info {
  padding-top: 10px;
  margin: 10px 20px;
  border-top: 2px solid;
  border-color: #e1dfdf;
  text-align: left;
}

.info-pic {
  margin: 20px 20px;
  text-align: left;
}

img {
  max-width: 100%;
}

.img-600 {
  max-width: 600px;
}

.letter {
  font-size: 50px;
  width: 40px;
  background-color: rgba(238, 174, 202, 0.4);
  padding: 20px 20px;
  margin: 5px 5px;
}

.space {
  color: transparent;
  background-color: transparent !important;
}

.block {
  padding: 20px 50px;
  width: 150px;
  margin: 5px 0px;
}

.board {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  width: 100%;
  min-height: 50px;
  background-color: rgba(255,255,255,0.30);
  justify-content: center;
}

.left-half {
  grid-area: 1 / 1 / 4 / 5;
}

.right-half {
  grid-area: 1 / 5 / 4 / 9;
  /* background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(252,70,107,1) 100%); */
}



h1, h2, h3, h4, h5, h6, p, div {
  color: #282828	;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.5
}

h1, h2, h3 {
  font-weight: 400;
}

h2, h2 {
  line-height: 1.25;
  font-size: 1.5rem;
}

.pink-text {
  color: rgb(252, 138, 157);
}

.card {
  border: 3px solid;
  border-color: 	#e3d4b6;
  border-radius: 10px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 50px;
  background-color: white;
}

.label {
  padding-bottom: 30px;
}

.bold {
  font-weight: 600;
}

.full {
  width: 100%;;
}

/* .pt {
  padding-top: 10px;
} */
.pt {
  padding-top: 20px;
}

.pb {
  padding-bottom: 40px;
}

.round {
  border-radius: 50%;
}


.navy {
  background: linear-gradient(225deg,#00001c,#00004c);
}

/* yummy green rgba(120, 231, 179, 0.5)*/

.pink {
  background-color: rgba(238,174,202,0.4);
}

.pink-yellow {
  background: linear-gradient(90deg, rgba(238,174,202,1) 0%, rgba(233,228,148,1) 100%);
}

.white-blue {
  background: linear-gradient(90deg, rgba(242,250,255,1) 0%, rgba(172,241,255,1) 100%);
}

.blue {
  background-color: #4d98ff;
}

.candy {
  background: linear-gradient(90deg, rgba(100,230,144,1) 0%, rgba(223,253,29,1) 50%, rgba(252,187,69,1) 100%);
}

.jolly {
  background: linear-gradient(45deg,#ff5c4c,#cd23a4 49.4%,#7e46fb);
}



